@import url(https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Montserrat:wght@200&family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pushster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;scroll-behavior:smooth}body{background-image:url(../../static/media/background1.4732a456.svg);background-position:center;background-size:cover;background-attachment:fixed}.loading{font-family:"Homemade Apple",cursive;font-weight:lighter;word-spacing:6px;text-transform:none}.item-list-title{font-family:"Homemade Apple",cursive;font-weight:lighter;word-spacing:6px;text-transform:none}
.btn-shopping-cart{border-radius:28px;background-color:#d7d7d9;width:100%;height:100%;padding:8px;display:flex;justify-content:center;align-items:center;cursor:pointer;box-shadow:0 4px 4px -2px #a65d6a}
header{background-color:#d9a7b4;display:flex;flex-direction:row;justify-content:space-between;align-items:center;box-shadow:0 4px 4px -2px #a65d6a;padding-left:12px;padding-right:12px;padding-top:12px;padding-bottom:12px}header nav{width:50%}header nav ul{list-style:none;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;padding:0;margin:0}header nav ul li a{color:#000;text-decoration:none;font-family:"Montserrat",sans-serif;font-weight:bold}header nav ul li a:hover{border-bottom:1px solid #000}header nav ul li a:visited{color:unset}.brand{font-family:"Oswald",sans-serif;text-transform:uppercase;color:#343140}.sub-h1{color:#000}.link{text-decoration:none}#dropdown-basic{display:flex;flex-direction:row;justify-content:center;align-items:center}.dropdown-name{margin-bottom:0}.dropdown-name:hover{border-bottom:1px solid #000}.dropdownLink:hover{border-bottom:none}
.banner{width:100%;background-color:#000;margin:0;padding:2px;text-align:center;color:#fff;text-transform:uppercase;font-size:.7rem}.banner p{margin:0;padding:0}
.greeting{width:100%;height:75vh;display:flex;justify-content:center;align-items:center}.loading{font-family:"Homemade Apple",cursive;font-weight:lighter;word-spacing:6px}
h2{font-family:"Homemade Apple",cursive;font-size:2rem;-webkit-text-decoration:#a65d6a underline;text-decoration:#a65d6a underline}.item-list-title{font-family:"Homemade Apple",cursive;font-weight:lighter;margin-top:20px;font-size:2rem;-webkit-text-decoration:#a65d6a underline;text-decoration:#a65d6a underline}
.product-item{width:20rem}.card-title{font-family:"Oswald",sans-serif;font-size:1.5rem;text-transform:uppercase;color:#000;text-decoration:none}.card-text{text-align:center}
.footer{background-color:#343140;color:#d7d7d9;font-family:"Oswald",sans-serif}.footer h4{margin:0;padding:0;color:#d9a7b4;text-transform:uppercase;font-weight:lighter}.footer a{text-decoration:none;color:unset}.footer a:visited{color:unset}.footer a:hover{color:#f1f1f2}.footer .heart{color:#a65d6a}
.card{padding:16px}.loading{font-family:"Homemade Apple",cursive}
.ItemDetail{width:100%;display:flex;justify-content:center;align-items:center}.ItemImg{width:500px}.item-detail{background-color:#f1f1f2;border:1px solid #d7d7d9;box-shadow:10px 12px 22px -12px rgba(0,0,0,.75);-webkit-box-shadow:10px 12px 22px -12px rgba(0,0,0,.75);-moz-box-shadow:10px 12px 22px -12px rgba(0,0,0,.75)}.ItemDetail-info{padding:8px}.detail-title{font-family:"Oswald",sans-serif;font-size:2rem;text-transform:uppercase;color:#000;color:#a65d6a;text-decoration:none}h3,h4{font-family:"Oswald",sans-serif;font-size:1.5rem;text-transform:uppercase;color:#000}.detail-attr{font-size:1.1rem;font-family:"Montserrat",sans-serif;font-weight:bold}.detail-price{font-family:"Oswald",sans-serif;font-weight:200;font-size:1.5rem;text-transform:uppercase;color:#000}span{color:#000;text-decoration:none}
.itemCount-container{display:flex;justify-content:center;align-items:center}.itemCount-container .itemCount .operations{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:8px}.itemCount-container .itemCount .operations span{margin-bottom:0}
h2{font-family:"Homemade Apple",cursive;-webkit-text-decoration:#a65d6a underline;text-decoration:#a65d6a underline;text-transform:none;font-size:2rem}td{padding:8px;text-align:left}td .btn-danger{background-color:#dc3545;border-color:#dc3545}
.swal2-title{margin:0px;font-size:2rem;box-shadow:0px 1px 1px rgba(0,0,0,.21);margin-bottom:28px;font-family:"Oswald",sans-serif;text-transform:uppercase;text-decoration:none}.swal2-confirm{background-color:#2cb155 !important}
small{color:#b22222;font-weight:bold;text-transform:uppercase;font-size:80%}.invalid-form{border-color:#b22222}.checkout-form{background-color:#f1f1f2;border:1px solid #d7d7d9;box-shadow:10px 12px 22px -12px rgba(0,0,0,.75);-webkit-box-shadow:10px 12px 22px -12px rgba(0,0,0,.75);-moz-box-shadow:10px 12px 22px -12px rgba(0,0,0,.75)}label{font-weight:bold;margin-right:12px;font-family:"Oswald",sans-serif;text-transform:uppercase}.btn-submit-order{background-color:#686673;font-weight:bold;font-family:"Oswald",sans-serif;text-transform:uppercase;color:#fff}.btn-submit-order:hover{-webkit-animation:button-hover 1s infinite;animation:button-hover 1s infinite}@-webkit-keyframes button-hover{0%{background-color:#686673;color:#fff}100%{background-color:#2cb054;color:#000}}@keyframes button-hover{0%{background-color:#686673;color:#fff}100%{background-color:#2cb054;color:#000}}
.hr-404{border:none;border-left:1px solid #73848c;height:100vh;width:1px}#page404{font-family:"Pushster",cursive}#page404 h4{font-family:"Pushster",cursive;width:300px;font-size:9rem;text-align:center;color:#343140}#page404 p{width:300px;font-size:3rem;text-align:center;color:#b87e88}#page404 .redirect{font-size:2rem;color:#686673}
